"use strict"
$('.menu__btn').click(() => {
    $('.menu__hamburger').toggleClass('menu__hamburger--active');
});
$('.header__loupe').on('click', ()=> {
  $('.header__search-row').slideToggle();
})

$('.header__search-close--button').on('click', ()=> {
  $('.header__search-row').slideUp();
})

$('.header__search-circle').on('click', ()=> {
  $('.header__search-row input').val("");
})
let inputs = document.querySelectorAll('.input-f');
Array.prototype.forEach.call(inputs, function (input) {
  let label = input.nextElementSibling,
    labelVal = label.querySelector('.input__text').innerText;

  input.addEventListener('change', function (e) {
    let countFiles = '';
    if (this.files && this.files.length >= 1)
      countFiles = this.files.length;

    if (countFiles)
      label.querySelector('.input__text').innerText = 'Выбрано файлов: ' + countFiles;
    else
      label.querySelector('.input__text').innerText = labelVal;
  });
});